import React, { useState } from 'react';
import styles from './Person.module.css';
import Aux from './hoc/Aux';

const Person = (props) => {
    const [showInfo, setShowInfo] = useState([false, "Show More"]);
    if (props.memba == null) { return null; }
    const mem = props.memba.data;

    const showMoreHandler = () => {
        const curr = showInfo[0];
        const str = (curr) ? "Show More" : "Show Less";
        setShowInfo([!curr, str]);
    }

    // console.log(mem)
    let extraInfo = showInfo[0] ? <Aux>
        <p>Mother: <strong>{mem.mother_name}</strong></p>
        <p>Place of Birth: <strong>{mem.pob}</strong></p>
        <p>Number: <strong>{mem.number}</strong></p>
        <p>Address: <strong>{mem.address}</strong></p>
        <p>Spouse Name: <strong>{mem.spouse_name}</strong></p>
        <p>Native Place: <strong>{mem.native_place}</strong></p>
        <p>Education: <strong>{mem.education}</strong></p>
    </Aux> : null;
    return (
        <div className={styles.Person}>
            <p>ID: <strong>{mem.key}</strong></p>
            <p>Name: <strong>{mem.first_name + " " + mem.last_name}</strong></p>
            <p>DOB: <strong>{mem.dob}</strong></p>
            <p>Father: <strong>{mem.father_name}</strong></p>
            {extraInfo}


            <button onClick={showMoreHandler}>
                    <b>{showInfo[1]}</b></button>
            <button onClick={() => props.getFatherClicked(props.memba, "father")}>Get father</button>
            <button onClick={() => props.getMotherClicked(props.memba, "mother")}>Get mother</button>
            <button onClick={() => props.getSpouseClicked(props.memba, "spouse")}>Get spouse</button>
            <button onClick={() => props.getChildrenClicked(props.memba)}>Show Children</button>
        </div>
    );
}

export default React.memo(Person);