import React from 'react';
import Person from '../person';
const Output = (props) => props.persons.map((memba) => {

    return <Person 
        getFatherClicked = {props.getRelative}
        getMotherClicked = {props.getRelative}
        getChildrenClicked={props.getChildrenHandler}
        getSpouseClicked = {props.getRelative}
        memba= {memba} 
        key={memba.key}/>
});

export default Output;